export default {
  name: 'tableColumn',

  data () {
    return {
      isShow: true,
    }
  },

  props: {
    columns: {
      type: Array,
      default: () => ([])
    },
    tableData: {
      type: Array,
      default: () => ([])
    },
    item: {
      type: Object,
      default: null,
    },
    /** 
     * body
    */
    tableBody: {
      type: String,
      default: ''
    },
    /** 
     * 
    */
    isChecked: {
      type: Boolean,
      default: false,
    },
    /** 
     * key,children
    */
    isChildren: {
      type: String,
      default: 'children',
    },
    /** 
     * treeData
    */
    defaultExpandAll: {
      type: Boolean,
      default: false,
    },
    /** 
     * 
    */
    indent: {
      type: Number,
      default: 0,
    },
  },

  components: {
  },

  computed: {
    expandAll () {
      return this.defaultExpandAll && this.isShow;
    },
  },

  mounted() {
    this.$nextTick(() => {
      
    })
  },

  methods: {
    handleArrow(item) {
      let refs = `columns-${item.indent}`;
      this.isShow = !this.isShow;
      this.$refs[refs].style.display = this.expandAll ? 'contents' : 'none';
    }
  },
}