export default {
  props: {
    /**
     * @desc ,
     */
    total: {
      type: Number,
      default: 0,
    },
    /** 
     * 
    */
    showTotal: {
      type: Boolean,
      default: false,
    },
    /** 
     * 
    */
    pageSize: {
      type: Number,
      default: 10,
    },
    /** 
     * 
    */
    pageCount: {
      type: Number,
      default: 0,
    },
    /** 
     * 
    */
    disabled: {
      type: Boolean,
      default: false,
    },
    /** 
     * 
    */
    currentPage: {
      type: Number,
      default: 1,
    },
    /** 
     * 
    */
    pagerCount: {
      type: Number,
      validator(value) {
        return value > 4 && value < 22 && (value % 2) === 1;
      },
      default: 7,
    },
    /** 
     * 
    */
    jump: {
      type: Boolean,
      default: false,
    },
    /** 
     * 
    */
    background: {
      type: Boolean,
      default: true,
    }
  },

  data() {
    return {
      internalCurrentPage: 1,
      internalPageSize: 10,
    };
  },

  computed: {
    internalPageCount() {
      if (typeof this.total === 'number') {
        return Math.max(1, Math.ceil(this.total / this.pageSize))
      } else if (typeof this.pageCount === 'number') {
        return Math.max(1, this.pageCount);
      }

      return null;
    },
  },

  watch: {
    currentPage: {
      // watch
      immediate: true,
      handler(val) {
        this.internalCurrentPage = val;
      },
    },

    pageSize: {
      immediate: true,
      handler(val) {
        this.internalPageSize = isNaN(val) ? 10 : val;
      }, 
    },
  },

  components: {
    'com-pager': require('./com/pager.vue').default,
  },

  created() {
  },

  mounted() {
  },

  beforeDestroy() {
  },

  methods: {
    // 
    handleChangePage (val) {
      this.internalCurrentPage = val;
      this.emitChange();
    },
    // 
    handlePre () {
      if (this.disabled || this.internalCurrentPage <= 1) return;
      this.internalCurrentPage = this.internalCurrentPage - 1;
      this.$emit('pre-click', this.internalCurrentPage);
      this.emitChange();
    },
    // 
    handleNext () {
      if (this.disabled || this.internalCurrentPage >= this.internalPageCount) return;
      this.internalCurrentPage = this.internalCurrentPage + 1;
      this.$emit('next-click', this.internalCurrentPage);
      this.emitChange();
    },

    emitChange () {
      this.$nextTick(() => {
        this.$emit('current-change', this.internalCurrentPage);
      })
    },

    // 
    handleIptChange (e) {
      let val = Number(e.target.value);
      this.internalCurrentPage = val;
      this.emitChange();
    },
  },
}
